import React, { Component } from 'react';

import { Row, Col } from 'react-bootstrap';

export default class AboutMeForm extends Component {
  constructor(props) {
    super(props);
    this.state = { name: '', email: '' };

    this.handleNameChange = this.handleNameChange.bind(this);
    this.handleEmailChange = this.handleEmailChange.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  handleNameChange(event) {
    this.setState({ name: event.target.value });
  }
  handleEmailChange(event) {
    this.setState({ email: event.target.value });
  }

  handleSubmit(event) {
    alert('Name: ' + this.state.name + '  Email' + this.state.email);
    this.setState({
      name: '',
      email: ''
    });
    event.preventDefault();
  }

  render() {
    return (
      <Row className="justify-content-center">
        <Col lg={6}>
          <p className="home-aboutme-form-text">
            Here you can add your contacts, to get an email notification and consultation from me
            </p>
        </Col>
        <Col lg={6}>
          <form action="submit" className="about-me-form" onSubmit={this.handleSubmit}>
            <input type="text" className="about-me-form-input" placeholder="Your Name" onChange={this.handleNameChange} value={this.state.name} />
            <input type="email" className="about-me-form-input" placeholder="Your Email" onChange={this.handleEmailChange} value={this.state.email} />
            <button type="submit" className="about-me-form-submit" value="Submit">
              <span>
                Send
              </span>
            </button>
          </form>
        </Col>
      </Row>
    )
  }
}
