import React, { Component } from 'react'

import { Container, Row, Col } from 'react-bootstrap';

import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBook, faMicrochip } from '@fortawesome/free-solid-svg-icons';
import { faRavelry } from '@fortawesome/free-brands-svg-icons';

export default class HomeSkillsSection extends Component {

    render() {

        const HomeSkill = [
            {
                icon: faMicrochip,
                title: 'Web Programming',
                text: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Ut distinctio nam vel enim ipsa in. Illo voluptatem quasi quo corrupti. Lorem ipsum dolor, sit amet consectetur adipisicing elit. Laudantium, magni!'
            },
            {
                icon: faBook,
                title: 'Landing Pages Creation',
                text: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Ut distinctio nam vel enim ipsa in. Illo voluptatem quasi quo corrupti. Lorem ipsum dolor, sit amet consectetur adipisicing elit. Laudantium, magni!'
            },
            {
                icon: faRavelry,
                title: 'Web Applications',
                text: 'Lorem ipsum dolor sit amet consectetur adipisicing elit. Ut distinctio nam vel enim ipsa in. Illo voluptatem quasi quo corrupti. Lorem ipsum dolor, sit amet consectetur adipisicing elit. Laudantium, magni!'
            },
        ]

        const HomeSkills = HomeSkill.map(item => {
            return (
                <Col lg={4} key={item.title.toString()}>
                    <div className="home-skills-card">
                        <FontAwesomeIcon icon={item.icon} className="home-skills-card-icon" />
                        <h3 className="home-skills-card-title">{item.title}</h3>
                        <p className="home-skills-card-text">{item.text}</p>
                    </div>
                </Col>
            )
        });



        return (
            <section className="home-skills">
                <Container>
                    <Row className="justify-content-center">
                        {HomeSkills}
                    </Row>
                </Container>
            </section>
        )
    }
}
