import React, { Component } from 'react';
import { Container } from 'react-bootstrap';

import AboutMeCard from './AboutMeCard.js';

export default class HomeAboutMeSection extends Component {

    render() {
        return (
            <section className="about-me">
                <Container>
                    <h2 className="about-me-title text-shadow">About Me</h2>
                    <p className="about-me-subtitle text-shadow">
                        Here is some information about me
                        </p>
                    <AboutMeCard />
                </Container>
            </section>

        )
    }
}
