import React, { Component } from 'react';

import { Container } from 'react-bootstrap';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

import { faHtml5, faCss3, faJs, faSass, faPython, faReact } from '@fortawesome/free-brands-svg-icons';
import { faChevronLeft, faChevronRight } from '@fortawesome/free-solid-svg-icons';

export default class WhatIDoCarousel extends Component {

    constructor(props) {
        super(props);



        this.handleNextClick = this.handleNextClick.bind(this);
        this.handlePrevClick = this.handlePrevClick.bind(this);

        this.handleDotClick = this.handleDotClick.bind(this);

        this.state = {
            autoplay: true,
            current_slide_index: 0,
            card_active_class: 'whatido-card',
            dots_class: [
                'whatido-carousel-dot active',
                'whatido-carousel-dot',
                'whatido-carousel-dot',
                'whatido-carousel-dot',
                'whatido-carousel-dot',
                'whatido-carousel-dot'
            ],
            images: [faHtml5, faCss3, faSass, faJs, faReact, faPython],
            subtitles: [
                'html',
                'css',
                'sass',
                'java script',
                'react.js',
                'python'
            ],
            text: [
                'Hypertext Markup Language (HTML) is the standard markup language for documents designed to be displayed in a web browser. It can be assisted by technologies such as Cascading Style Sheets (CSS) and scripting languages such as JavaScript.',

                'Cascading Style Sheets (CSS) is a style sheet language used for describing the presentation of a document written in a markup language like HTML.[1] CSS is a cornerstone technology of the World Wide Web, alongside HTML and JavaScript.',

                "CSS on its own can be fun, but stylesheets are getting larger, more complex, and harder to maintain. This is where a preprocessor can help. Sass lets you use features that don't exist in CSS yet like variables, nesting, mixins, inheritance and other nifty goodies that make writing CSS fun again.",

                'JavaScript, often abbreviated as JS, is a programming language that conforms to the ECMAScript specification. JavaScript is high-level, often just-in-time compiled, and multi-paradigm. It has curly-bracket syntax, dynamic typing, prototype-based object-orientation, and first-class functions.',

                'React (also known as React.js or ReactJS) is an open-source JavaScript library for building user interfaces. It is maintained by Facebook and a community of individual developers and companies.React can be used as a base in the development of single-page or mobile applications.',

                'Python is dynamically typed and garbage-collected. It supports multiple programming paradigms, including structured (particularly, procedural), object-oriented, and functional programming. Python is often described as a "batteries included" language due to its comprehensive standard library.'
            ]
        };


    }

    handleNextClick() {
        if (this.state.current_slide_index > this.state.images.length - 2) {
            this.setState({ current_slide_index: 0 });
        }
        else {
            this.setState({
                current_slide_index: this.state.current_slide_index + 1
            });
        }

        setTimeout(() => {
            this.setState({
                card_active_class: 'whatido-card'
            });
        }, 500);

        this.setState({
            card_active_class: 'whatido-card active'
        });

    }

    handlePrevClick() {
        if (this.state.current_slide_index < 1) {
            this.setState({ current_slide_index: this.state.images.length - 1 });
        }
        else {
            this.setState({
                current_slide_index: this.state.current_slide_index - 1
            });
        }

        setTimeout(() => {
            this.setState({
                card_active_class: 'whatido-card'
            });
        }, 500);

        this.setState({
            card_active_class: 'whatido-card active'
        });
    }

    handleDotClick(e) {
        let data = Number(e.target.attributes['datadotindex'].value);

        this.setState({
            current_slide_index: data
        });
    }

    render() {

        let dots_active_classes = [
            {
                class: 'whatido-carousel-dot',
                dot_index: 0
            },
            {
                class: 'whatido-carousel-dot',
                dot_index: 1
            },
            {
                class: 'whatido-carousel-dot',
                dot_index: 2
            },
            {
                class: 'whatido-carousel-dot',
                dot_index: 3
            },
            {
                class: 'whatido-carousel-dot',
                dot_index: 4
            },
            {
                class: 'whatido-carousel-dot',
                dot_index: 5
            },
        ];

        dots_active_classes[this.state.current_slide_index].class = 'whatido-carousel-dot active';

        let carousel_dots = dots_active_classes.map(item => {
            return (
                <span className={item.class} key={item.dot_index.toString()} datadotindex={item.dot_index} onClick={this.handleDotClick}></span>
            )
        });

        return (
            <div className="whatido-carousel">
                <Container>
                    <div className="whatido-carousel-border">
                        <div className={this.state.card_active_class}>
                            <div className="whatido-card-title">
                                <FontAwesomeIcon className="whatido-card-icon" icon={this.state.images[this.state.current_slide_index]} />
                                <h3 className="whatido-card-subtitle text-shadow">
                                    {this.state.subtitles[this.state.current_slide_index]}
                                </h3>
                            </div>
                            <p className="whatido-card-text">
                                {this.state.text[this.state.current_slide_index]}
                            </p>
                        </div>
                    </div>

                </Container>
                <div className="whatido-carousel-buttons">
                    <span href="#!" className="whatido-carousel-buttons-control" onClick={this.handlePrevClick}>
                        <FontAwesomeIcon icon={faChevronLeft} />
                    </span>


                    <span href="#!" className="whatido-carousel-buttons-control" onClick={this.handleNextClick}>
                        <FontAwesomeIcon icon={faChevronRight} />
                    </span>
                </div>

                <div className="whatido-carousel-dots">
                    {carousel_dots}
                </div>
            </div>
        )
    }
}
