import React, { Component } from 'react';



import HomeQuotesImg_1 from '../../../../media/photos/zuckerberk-img.jpg';
import HomeQuotesImg_2 from '../../../../media/photos/billgates-img.jpg';
import HomeQuotesImg_3 from '../../../../media/photos/stevejobs-img.jpg';
import HomeQuotesImg_4 from '../../../../media/photos/robertdowney-img.jpg';

export default class HomeQuotesSection extends Component {

    constructor(props) {
        super(props);

        this.handleDotClick = this.handleDotClick.bind(this);
        this.handleNextClick = this.handleNextClick.bind(this);

        this.state = {
            autoplay: true,
            current_slide_index: 0,
            dots_class: [
                'home-quotes-carousel-dot active',
                'home-quotes-carousel-dot',
                'home-quotes-carousel-dot',
                'home-quotes-carousel-dot'
            ],
            carousel_card_overlay_classes: 'home-quotes-carousel-card-overlay',
            images: [
                HomeQuotesImg_1,
                HomeQuotesImg_2,
                HomeQuotesImg_3,
                HomeQuotesImg_4
            ],
            titles: [
                'Mark Zuckerberg',
                'Bill Gates',
                'Steve Jobs',
                'Robert Downey Jr.'
            ],
            subtitles: [
                'Media Magnate, Facebook co-founder, Philanthropist',
                'Business magnate, Software Developer, Microsoft co-founder, Investor',
                'Industrial Designer, Media Proprietor, Apple co-founder',
                "Listed by Forbes as Hollywood's highest-paid actor"
            ],
            text: [
                '"Lorem ipsum dolor sit amet consectetur adipisicing elit. Doloremque quaerat tempora incidunt perspiciatis sequi accusamus numquam nostrum cumque, modi esse. Ducimus, eveniet odio veniam iste praesentium veritatis quidem possimus optio non error, quod quibusdam aperiam quis rerum earum velit inventore voluptatum sed est beatae. Quidem veritatis ratione neque. Animi, magni!"',
                '"Lorem ipsum dolor sit amet consectetur adipisicing elit. Doloremque quaerat tempora incidunt perspiciatis sequi accusamus numquam nostrum cumque, modi esse. Ducimus, eveniet odio veniam iste praesentium veritatis quidem possimus optio non error, quod quibusdam aperiam quis rerum earum velit inventore voluptatum sed est beatae. Quidem veritatis ratione neque. Animi, magni!"',
                '"Lorem ipsum dolor sit amet consectetur adipisicing elit. Doloremque quaerat tempora incidunt perspiciatis sequi accusamus numquam nostrum cumque, modi esse. Ducimus, eveniet odio veniam iste praesentium veritatis quidem possimus optio non error, quod quibusdam aperiam quis rerum earum velit inventore voluptatum sed est beatae. Quidem veritatis ratione neque. Animi, magni!"',
                '"Lorem ipsum dolor sit amet consectetur adipisicing elit. Doloremque quaerat tempora incidunt perspiciatis sequi accusamus numquam nostrum cumque, modi esse. Ducimus, eveniet odio veniam iste praesentium veritatis quidem possimus optio non error, quod quibusdam aperiam quis rerum earum velit inventore voluptatum sed est beatae. Quidem veritatis ratione neque. Animi, magni!"'
            ]
        };
    }

    handleDotClick(e) {
        let data = Number(e.target.attributes['datadotindex'].value);

        this.setState({
            carousel_card_overlay_classes: 'home-quotes-carousel-card-overlay active'
        });

        setTimeout(() => {
            this.setState({
                current_slide_index: data
            });
        }, 800);

        setTimeout(() => {
            this.setState({
                carousel_card_overlay_classes: 'home-quotes-carousel-card-overlay'
            });
        }, 2000);

        clearInterval(this.timerId);
        this.timerId = setInterval(this.handleNextClick, 5000);
    }

    handleNextClick() {
        this.setState({
            carousel_card_overlay_classes: 'home-quotes-carousel-card-overlay active'
        });
        if (this.state.current_slide_index > this.state.images.length - 2) {
            setTimeout(() => {
                this.setState({
                    current_slide_index: 0
                });
            }, 800);
        }
        else {
            setTimeout(() => {
                this.setState({
                    current_slide_index: this.state.current_slide_index + 1
                });
            }, 800);
        }
        setTimeout(() => {
            this.setState({
                carousel_card_overlay_classes: 'home-quotes-carousel-card-overlay'
            });
        }, 2000);
    }

    componentDidMount() {
        this.timerId = setInterval(this.handleNextClick, 5000);
    }

    componentWillMount() {
        clearInterval(this.timerId);
    }


    render() {

        let dots_active_classes = [
            {
                class: 'home-quotes-carousel-dot',
                dot_index: 0
            },
            {
                class: 'home-quotes-carousel-dot',
                dot_index: 1
            },
            {
                class: 'home-quotes-carousel-dot',
                dot_index: 2
            },
            {
                class: 'home-quotes-carousel-dot',
                dot_index: 3
            }
        ];

        dots_active_classes[this.state.current_slide_index].class = 'home-quotes-carousel-dot active';

        let carousel_dots = dots_active_classes.map(item => {
            return (
                <span className={item.class} key={item.dot_index.toString()} datadotindex={item.dot_index} onClick={this.handleDotClick}></span>
            )
        });



        return (
            <section className="home-quotes">
                <div className="home-quotes-carousel">
                    <div className="home-quotes-carousel-card">
                        <div className="home-quotes-carousel-img"  >
                            <div className="overlay">
                            </div>
                            <img src={this.state.images[this.state.current_slide_index]} alt="" />
                        </div>
                        <div className="home-quotes-carousel-content">
                            <h2 className="home-quotes-carousel-content-title">
                                {this.state.titles[this.state.current_slide_index]}
                            </h2>
                            <h3 className="home-quotes-carousel-content-subtitle">
                                {this.state.subtitles[this.state.current_slide_index]}
                            </h3>
                            <p className="home-quotes-carousel-content-text">
                                {this.state.text[this.state.current_slide_index]}
                            </p>

                            <div className="home-quotes-carousel-dots">
                                {carousel_dots}
                            </div>
                        </div>
                        <div className={this.state.carousel_card_overlay_classes}>
                            
                        </div>
                    </div>
                </div>
            </section>
        )
    }
}
